/*---------------------------------------------------------*/
/*  ALERTS
/*---------------------------------------------------------*/
.alert{
	background: 					$alert-bg;
  border-color: 				$alert-border-color;
  color: 								$alert-color;
  font-size: 						$alert-font-size;
  line-height: 					$alert-line-height;
  padding: 							$alert-padding-y $alert-padding-x;
  border-radius: 				$global-radius;
  box-shadow: 					$alert-shadow;
  position: 						relative;
  @include clearfix;



	a,
	.alert-link{
		color: 							inherit;
		text-decoration: 		underline;
		font-weight: 				inherit;
	}


	button.close{
		font-size: 					$alert-close-size;
		line-height: 				$alert-close-size/2;
		font-weight: 				300;
		opacity: 						0.8;
		box-shadow: 				none;
	}//close



  &.alert-dismissable, 
  &.alert-dismissible{
    padding-right: 			35px;
  }//alert-dismissible




	// ALERT BLOCK
	.alert-block{
		width: 							calc(100% - #{$alert-image-size});
		float: 							right;

		&:first-child{
			padding-left: 		0;
			float: 						left;
		}//first-child
	}//alert-block


	&.alert-rounded .alert-block{
		padding-right: 			10px;

		&:first-child{ padding-left: 10px; }
	}//alert-rounded alert-block



}//alert









