/*-------------------------------------------------*/
/*	BADGES
/*-------------------------------------------------*/
.badge{
	border: 				solid 1px #ddd;
	font-weight: 		$badge-font-weight;
	line-height: 		$badge-line-height;


	&:not(.badge-pill){ border-radius: $global-radius; }

	&.badge-primary 	{background: $brand-primary; border-color: $brand-primary;}
	&.badge-secondary {border-color: #6c757d;}
	&.badge-success 	{border-color: $brand-success;}
	&.badge-info 			{border-color: $brand-info;}
	&.badge-warning 	{border-color: $brand-warning; color: #fff;}
	&.badge-danger 		{border-color: $brand-danger;}
	&.badge-light 		{border-color: #fff;}
	&.badge-dark 			{border-color: #000;}
}//badge




// BADGE ALERTS
[data-badge]{
	position: relative;

	&::after{
		content: 				attr(data-badge);
		background: 		$brand-danger;
		color: 					#fff;
		font-size: 			75%;
		font-weight: 		$badge-font-weight;
		line-height: 		$badge-alert-min-size;
		min-width: 			$badge-alert-min-size;
		min-height: 		$badge-alert-min-size;
		padding: 				2px 7px;
		right: 					-$badge-alert-min-size / 2.5;
		top: 						-$badge-alert-min-size / 2.5;
		position: 			absolute;
		border-radius: 	$global-radius * 100;
	}


	&.animated{
		&::after{
			-webkit-animation: 	burst 2s infinite linear;
			animation: 					burst 2s infinite linear
		}
	}

}//[data-badge]





@-webkit-keyframes burst{
	0%   { opacity: 0; }
	80%  { opacity: 1; }
	100% { opacity: 1; }
}

@-moz-keyframes burst{
	0%   { opacity: 0; }
	80%  { opacity: 1; }
	100% { opacity: 1; }
}

@keyframes burst{
	0%   { opacity: 0; }
	80%  { opacity: 1; }
	100% { opacity: 1; }
}








/*-------------------------------------------------*/
/*	PROGRESS BARS
/*-------------------------------------------------*/
.progress{
	background-color: 	$progress-background;
	height: 						$progress-height;
	margin-bottom: 			$progress-margin;
	border-radius: 			$progress-rounded;
	box-shadow: 				$progress-box-shadow;
	overflow: 					visible;


	.progress-bar{
		background-color: 	$progress-bar-background;
		position: 					relative;
		border-radius: 			$progress-rounded;
		box-shadow: 				$progress-box-shadow;


		> span:not(.sr-only){
			background: 		#fff;
			border: 					solid 3px #BDC3C8;
			color: 					#666;
			font-size: 				11.5px;
			font-weight: 			700;
			line-height: 			34px;
			text-align: 			center;
			width: 						34px;
			height: 					34px;
			right: 						0;
			top: 							-15px;
			position: 				absolute;
			white-space: 			nowrap;
			border-radius: 		300px;
			box-sizing: 			content-box;
		}// > span:not(.sr-only)


		&.progress-bar-primary{
			background-color: $brand-primary;

			> span:not(.sr-only){ border-color: $brand-primary; }
		}

		&.progress-bar-info{
			background-color: $brand-info;

			> span:not(.sr-only){ border-color: $brand-info; }
		}

		&.progress-bar-success{
			background-color: $brand-success;

			> span:not(.sr-only){ border-color: $brand-success; }
		}

		&.progress-bar-warning{
			background-color: $brand-warning;

			> span:not(.sr-only){ border-color: $brand-warning; }
		}

		&.progress-bar-danger{
			background-color: $brand-danger;

			> span:not(.sr-only){ border-color: $brand-danger; }
		}

	}//progress-bar

}//progress










/*---------------------------------------------------------*/
/*  TOOLTIPS
/*---------------------------------------------------------*/
.tooltip{
  font-size: 		$tooltip-font-size;
  font-weight: 	$tooltip-font-weight;
}//tooltip


// Wrapper for the tooltip content
.tooltip-inner{
	color: 				$tooltip-color;
  padding: 			$tooltip-spacer-y $tooltip-spacer-x;
}//tooltip-inner