/*---------------------------------------------------------*/
/*	FORMS
/*---------------------------------------------------------*/
$form-background: 									#fff !default;
$form-color: 												$gray-dark !default;
$form-border: 											1px !default;
$form-border-color: 								$gray-light !default;
$form-font-size: 										.9rem !default;
$form-line-height: 									1.5 !default;
$form-padding-y: 										10px !default;
$form-padding-x: 										14px !default;

$form-disabled-opacity: 						.7 !default;



// FOCUS
$form-background-focus: 						$form-background !default;
$form-border-color-focus: 					$gray !default;

// PLACEHOLDER
$form-placeholder-color: 						lighten($gray, 40%) !default;
$form-placeholder-color-focus: 			transparent !default;

// SM
$form-sm-font-size: 								.8rem !default;
$form-sm-padding-y: 								7px !default;
$form-sm-padding-x: 								12px !default;

// LG
$form-lg-font-size: 								1.2rem !default;
$form-lg-padding-y: 								14px !default;
$form-lg-padding-x: 								16px !default;



// LABEL
$form-label-color: 									$gray-dark !default;
$form-label-font-size: 							.9rem !default;
$form-label-font-weight: 						400 !default;


// HELP
$form-help-color: 									darken($gray-light, 10%) !default;



// FORM ROUNDED
$form-rounded-radius: 							$global-radius * 100 !default;





// INPUT GROUPS
$form-ig-btn-icon: 									20px !default;
$form-ig-btn-size: 									43px !default;

$form-ig-sm-btn-icon: 							18px !default;
$form-ig-sm-btn-size: 							35px !default;
$form-ig-sm-btn-line-height: 				18px !default;

$form-ig-lg-btn-icon: 							26px !default;
$form-ig-lg-btn-size: 							58px !default;
$form-ig-lg-btn-line-height: 				19px !default;




// CHECKBOX
$checkbox-color: 										$gray !default;
$checkbox-size: 										16px !default;
$checkbox-padding: 									$checkbox-size + 10 !default;
$checkbox-radio-margin-top: 				0 !default;
$checkbox-radio-margin-bottom: 			10px !default;




// RADIO
$radio-color: 											$checkbox-color !default;
$radio-size: 												$checkbox-size !default;
$radio-padding: 										$checkbox-size + 10 !default;



// TOGGLE
$toggle-color: 											#e1e1e1 !default;
$toggle-width: 											40px !default;
$toggle-height: 										22px !default;
$toggle-line-height: 								28px !default;




// TOUCHSPIN
$touchspin-font-size: 							20px !default;
$touchspin-btn-font-weight: 				300 !default;
$touchspin-btn-sm-height: 					35px !default;
$touchspin-btn-lg-height: 					58px !default;




// DATETIMEPICKER
$datepicker-spacer-x: 							20px !default;
$datepicker-spacer-y: 							10px !default;
$datepicker-font-size: 							12px !default;
$datepicker-font-weight: 						400 !default;
$datepicker-day-width: 							29px !default;
$datepicker-day-height: 						27px !default;

$datepicker-day-old-color: 				#ccc !default;
$datepicker-day-new-color: 				#999 !default;

$datepicker-dow-font-weight: 				700 !default;

$datetimepicker-width: 							250px !default;
$datetimepicker-padding: 						4px !default;
$datetimepicker-active-color: 			$brand-info !default;



// RANGE
$range-thumb-size: 									17px;
$range-thumb-border: 								2px;
$range-track-size: 									4px;
$range-track-radius: 								1000px;



// FLOATING LABELS
$form-fl-padding-top: 							20px;
$form-fl-padding-x: 								14px;
$form-fl-padding-bottom: 						10px;