/*---------------------------------------------------------*/
/*	BUTTONS
/*---------------------------------------------------------*/
@import 'buttons/btn-mixins';

// btn-style:
// - $background
// - $border
// - $color
// - $bgHover
// - $borderHover
// - $colorHover



// Default styles for buttons "without any class"
button{
	@include btn-style(
		$background: 		$gray-lighter,
		$border: 				$gray-lighter,
		$color: 				$gray,
		$bgHover: 			darken($gray-lighter, 5%),
		$borderHover: 	darken($gray-lighter, 5%),
		$colorHover: 		darken($gray, 10%)
	);


	font-size: 					$btn-font-size;
	font-weight: 				$btn-font-weight;
	letter-spacing: 		.5px;
	padding: 						$btn-spacer-y $btn-spacer-x;
	display:						inline-flex;
	justify-content: 		space-evenly;
	align-items: 				center;
	transition: 				$btn-transition;
	border-radius:			$global-radius;
}//button






.btn{
	// Include styles from the basic button element
	@extend button;


	@include linkHover(text-decoration none);

	// BUTTON PRIMARY
	&.btn-primary{ @include btn-style(
		$brand-primary, 
		$brand-primary, 
		#fff, 
		$brand-primary-darker, 
		$brand-primary-darker
	); }
	
	// BUTTON SECONDARY
	&.btn-secondary{ @include btn-style(
		$gray, 
		$gray, 
		#fff,
		$gray-dark,
		$gray-dark
	); }
	
	// BUTTON SUCCESS
	&.btn-success{ @include btn-style(
		$brand-success, 
		$brand-success, 
		#fff, 
		$brand-success-darker, 
		$brand-success-darker
	); }
	
	// BUTTON INFO
	&.btn-info{ @include btn-style(
		$brand-info, 
		$brand-info, 
		#fff, 
		$brand-info-darker, 
		$brand-info-darker
	); }
	
	// BUTTON WARNING
	&.btn-warning{ @include btn-style(
		$brand-warning, 
		$brand-warning, 
		#fff, 
		$brand-warning-darker, 
		$brand-warning-darker
	); }

	// BUTTON DANGER
	&.btn-danger{ @include btn-style(
		$brand-danger, 
		$brand-danger, 
		#fff, 
		$brand-danger-darker, 
		$brand-danger-darker
	); }

	// BUTTON LIGHT
	&.btn-light{ @include btn-style(
		rgba(#fff,.7), 
		rgba(#fff,.7), 
		#444, 
		#fff,
		#fff,
		#222
	); }
	
	// BUTTON DARK
	&.btn-dark{ @include btn-style(
		rgba(#000,.7), 
		rgba(#000,.2), 
		#fff, 
		#000, 
		#000, 
		#fff
	); }

	// BUTTON LINK
	&.btn-link{ 
		@include btn-style(
			transparent, 
			transparent, 
			$brand-info, 
			$colorHover: $brand-info-darker
		);

		font-weight: inherit;
	}




	// BUTTON ROUNDED
	&.btn-rounded{ border-radius: 1000px; }


	// BUTTON SHARP
	&.btn-sharp{ border-radius: 0 !important; }


}//btn



@import 'buttons/btn-outline';




/*-------------------------------------------------*/
/*	BUTTONS CONS
/*	Cons provide a fixed width for buttons
/*-------------------------------------------------*/
.btn-cons{
	min-width: $btn-cons-width;
}//btn-cons







/*-------------------------------------------------*/
/*  BUTTON SIZES
/*-------------------------------------------------*/
.btn-lg{
	font-size: 					$btn-lg-font-size;
	padding: 						$btn-lg-padding-y $btn-lg-padding-x;
}

.btn-sm{
	font-size: 					$btn-sm-font-size;
	font-weight: 				600;
	padding: 						$btn-sm-padding-y $btn-sm-padding-x;
}




/*-------------------------------------------------*/
/*  BUTTONS CLOSE
/*-------------------------------------------------*/
.close{
	background: transparent;
	border: none;


	@include linkHover(background-color transparent);

	button:active, 
	button:active:focus, 
	button:active:hover, 
	button:focus, 
	button:hover, 
	button:not(:disabled):not(.disabled).active, 
	button:not(:disabled):not(.disabled):active{
		background-color: transparent;
	}

}//close





@import 'buttons/btn-icons';
@import 'buttons/btn-dropdown';
