/*---------------------------------------------------------*/
/*	SIMPLE LIGHTBOX
/*---------------------------------------------------------*/
$sl-overlay-background: rgba(#fff,.9) !default;


$sl-btn-fill: transparent !default;
$sl-btn-stroke: #000 !default;
$sl-btn-stroke-width: 1px !default;

$sl-btn-close-fill: #000 !default;
$sl-btn-close-stroke: $sl-btn-stroke !default;
$sl-btn-close-stroke-width: 1.5px !default;