/*-------------------------------------------------*/
/*	BLUR
/*-------------------------------------------------*/
.h-blur{
	@extend .hover-element;

	.card-img{
		transform: scale(1.1);
	}

	.card-img-overlay{
		opacity: 0;
		visibility: hidden;
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			@include filter-blur(4px);
		}//card-img


		.card-img-overlay{
			opacity: 1;
			visibility: visible;
		}//card-img-overlay

	}//:hover

}//h-blur