/*---------------------------------------------------------*/
/*	BUTTON MIXINS
/*---------------------------------------------------------*/
@mixin btn-style(
	$background: 		$btn-background,
	$border: 				$btn-background,
	$color: 				$btn-color,
	$bgHover: 			darken($background, 6%),
	$borderHover: 	darken($background, 6%),
	$colorHover: 		$color
){
	background: 	$background;
	border: 			1px solid $border;
	color: 				$color;


	&:hover,
	&:focus,
	&:active,
	&:active:focus,
	&:active:hover,
	&.active,
	&.focus,
	&.active.focus,
	&:not(:disabled):not(.disabled).active, 
	&:not(:disabled):not(.disabled):active{
		background: 	$bgHover;
		border-color: $borderHover;
		color: 				$colorHover;
		box-shadow: 	none;
	}//:hover and :active



	&.disabled, 
	&.disabled:hover, 
	&.disabled:focus, 
	&.disabled:active:focus,
	&[disabled], 
	&[disabled]:hover, 
	&[disabled]:focus, 
	&[disabled]:active:focus{
		background: 	$background;
		border-color: $border;
		color: 				$color;
	}//disabled

}// @btn-style






// BTN OUTLINE
@mixin btn-outline-color(
	$color,
	$hoverFontColor: #fff
){
	background: 	transparent;
	border-color: $color;
	color: 				$color;
	border-width: 1.5px;
	opacity: 			0.7;


	&:hover{
		opacity: 1;
	}

	&.active.focus, &.active:focus,
	&.active:hover, &.focus:active,
	&:active:focus, &:active:hover,
	&:active,
	&:focus,
	&.active{
		background: 	transparent;
		border-color: $color;
		color: 				$color;
		opacity:			1;
	}

	&.disabled, &.disabled:hover, &.disabled:focus, &.disabled:active:focus,
	&[disabled],  &[disabled]:hover, &[disabled]:focus, &[disabled]:active:focus{
		background: 	transparent;
		border-color: $color;
		color: 				$color;
		opacity: 			0.7;
	}

}// @btn-outline-color





// CUSTOM COLORED BUTTONS
@mixin btn-color($color){
	background: 	$color;
	border-color: $color;

	&:hover{
		background: 	lighten($color, 10%);
		border-color: lighten($color, 10%);
	}//hover

	&:active,
	&:focus,
	&.active{
		background: 	darken($color,20%);
		border-color: darken($color,20%);
	}
}//btn-color






@mixin btn-dropdown(
	$border-color: 	darken($btn-background, 4%),
	$box-shadow: 		none
){
	&.active.focus,
	&.active:focus,
	&.active:hover,
	&.focus:active,
	&:active:focus,
	&:active:hover,
	&.dropdown-toggle.btn-default.focus,
	&.dropdown-toggle.btn-default:focus,
	&.dropdown-toggle.btn-default:hover,

	&.dropdown-toggle.btn-default{
		border-color: 	$border-color;
		box-shadow: 		$box-shadow;
	}//dropdown-toggle btn-default
}
