/*---------------------------------------------------------*/
/*	NAVS
/*---------------------------------------------------------*/
.nav{

	.nav-item.show .nav-link, 
	.nav-link.active{
		color: $brand-info;
	}// active


	.nav-link{
		color: inherit;

		&:hover, &:active, &:focus, &:active:focus{
			color: $brand-info;
		}// :hover
	}// nav-link


	.nav-link.disabled{
		color: $gray-light;
		cursor: not-allowed;
	}// disabled

}//nav





/*---------------------------------------------------------*/
/*	PILLS
/*---------------------------------------------------------*/
.nav-pills{

	.nav-link{
		color: inherit;
	}// nav-link


	.nav-link.active, 
	.show > .nav-link{
		background: $brand-info;
		color: #fff;
	}

}//nav-pills







/*---------------------------------------------------------*/
/*	TABS
/*---------------------------------------------------------*/
$tab-border-color: #dee2e6;


.nav-tabs{

	.nav-link{
		border-radius: 0;
	}// nav-link


	.nav-item.show .nav-link, 
	.nav-link.active{
		background: transparent;
		border-color: transparent;
		box-shadow: 0 -2px 0 $brand-info inset;
	}// active

	.nav-link:focus, 
	.nav-link:hover{
		border-color: transparent;
	}


}//nav-tabs








.nav-tabs.nav-tabs-bottom{
	border-top: 1px solid $tab-border-color;
	border-bottom: none;

	.nav-item{
		margin-top: -1px;
		margin-bottom: 0;
	}// nav-item


	.nav-item.show .nav-link, 
	.nav-link.active{
		box-shadow: 0 2px 0 $brand-info inset;
	}// active

}//nav-tabs-bottom






.nav-tabs.nav-tabs-left{
	flex-direction: column;
	border-right: 1px solid $tab-border-color;
	border-bottom: none;

	.nav-item{
		margin-bottom: 0;
		margin-right: -1px;
	}// nav-item


	.nav-item.show .nav-link, 
	.nav-link.active{
		box-shadow: -2px 0 0 $brand-info inset;
	}// active

}//nav-tabs-left





.nav-tabs.nav-tabs-right{
	flex-direction: column;
	border-left: 1px solid $tab-border-color;
	border-bottom: none;

	.nav-item{
		margin-bottom: 0;
		margin-left: -1px;
	}// nav-item


	.nav-item.show .nav-link, 
	.nav-link.active{
		box-shadow: 2px 0 0 $brand-info inset;
	}// active

}//nav-tabs-right