/*-------------------------------------------------*/
/*	HOVER IMAGES
/*-------------------------------------------------*/
$hover-transition: .3s ease-in-out !default;


// Styles for all hover elements
.hover-element{
	backface-visibility: hidden;
	perspective: 50em;
	transform: translateZ(0);
	overflow: hidden;


	.card-img{
		transition: $hover-transition;
	}//card-img


	.card-img-overlay{
		transition: $hover-transition;
	}//card-img-overlay


	*{
		transform-origin: 50% 50%;
	}

}//hover-element




@import 'hover/fade';
@import 'hover/scale';
@import 'hover/slide';
@import 'hover/rotate';
@import 'hover/filters';


















/*-------------------------------------------------*/
/*	SLIDE TOP
/*-------------------------------------------------*/
.h-slide-top{
	@extend .hover-element;

	.card-img{
		transition-delay: .1s;
	}//card-img

	.card-img-overlay{
		// transition: .5s ease-in-out;
		transform: translate3d(0,100%,0);
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: translate3d(0,-100%,0);
		}//card-img

		.card-img-overlay{
			transform: translate3d(0,0,0);
		}//card-img-overlay

	}//:hover

}//h-slide-top