/*-------------------------------------------------*/
/*	SLIDE UP
/*-------------------------------------------------*/
.h-slide-up{
	@extend .hover-element;

	.card-img{
		transform: scale(1.2);
		transform-origin: 50% 0;
	}//card-img

	.card-img-overlay{
		transform: translate3d(0,100%,0);
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform-origin: 50% 50%;
		}//card-img


		.card-img-overlay{
			transform: translate3d(0,0,0);
		}//card-img-overlay

	}//:hover

}//h-slide-up





/*-------------------------------------------------*/
/*	SLIDE DOWN
/*-------------------------------------------------*/
.h-slide-down{
	@extend .hover-element;

	.card-img{
		transform: scale(1.2);
		transform-origin: 50% 100%;
	}//card-img

	.card-img-overlay{
		transform: translate3d(0,-100%,0);
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform-origin: 50% 0;
		}//card-img


		.card-img-overlay{
			transform: translate3d(0,0,0);
		}//card-img-overlay

	}//:hover

}//h-slide-down






