/*---------------------------------------------------------*/
/*	TABLES
/*---------------------------------------------------------*/
$table-border-color: #dee2e6 !default;
$table-border: solid 1px $table-border-color !default;
$table-font-size: .9rem !default;

$thead-border: solid 2px $table-border-color !default;

$th-font-size: .8rem !default;

$td-font-size: .9rem !default;

$caption-font-size: 80% !default;




// STRIPED
$td-striped: rgba(#000,.05) !default;
$td-striped-dark: rgba(#fff,.05) !default;


// HOVER
$tr-hover-transition: .3s ease-in-out !default;