/*-------------------------------------------------*/
/*	SCALE IN
/*-------------------------------------------------*/
.h-scale-in{
	@extend .hover-element;


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1.4);
		}//card-img

	}//:hover

}//h-scale-in







/*-------------------------------------------------*/
/*	SCALE OUT
/*-------------------------------------------------*/
.h-scale-out{
	@extend .hover-element;

	.card-img{
		transform: scale(1.4);
	}//card-img



	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1);
		}//card-img

	}//:hover

}//h-scale-out








/*-------------------------------------------------*/
/*	SCALE IN 2
/*-------------------------------------------------*/
.h-scale-in-2{
	@extend .hover-element;


	.card-img-overlay{
		opacity: 0;
		visibility: hidden;
		perspective: 900px;
		border-radius: 0;
	}//card-img-overlay


	.card-img-overlay > *{
		transition: $hover-transition;
		transition-duration: .4s;
		transform: translate3d(0,0,-800px);
	}//card-img-overlay > *


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1.4);
		}//card-img

		.card-img-overlay{
			opacity: 1;
			visibility: visible;
		}//card-img-overlay

		.card-img-overlay > *{
			transform: translate3d(0,0,0);
		}//card-img-overlay

	}//:hover

}//h-scale-in-2







/*-------------------------------------------------*/
/*	SCALE OUT 2
/*-------------------------------------------------*/
.h-scale-out-2{
	@extend .h-fade-zoom;

	.card-img{
		transform: scale(1.4);
	}//card-img


	.card-img-overlay > *{
		transition: $hover-transition;
		transition-duration: .4s;
	}//card-img-overlay > *


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1);
		}//card-img

	}//:hover

}//h-scale-out-2







