	.sl-navigation{

		button{
			background-repeat: no-repeat; 
			background-position: center;
			font-size: 0 !important; 


			&:hover, &:active, &:focus, &:active:focus{
				background-color: transparent;
			}



			&.sl-prev{
				background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-1 0 13 24'><path d='M11 1l-9 10.56 9 11.44' stroke='#{$sl-btn-stroke}' fill='#{$sl-btn-fill}' stroke-width='#{$sl-btn-stroke-width}' stroke-linecap='round'/></svg>");
			}//sl-prev


			&.sl-next{
				background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='9 4 13 24'><path d='M11 5 20 15.9447962 11 27 11 27' stroke='#{$sl-btn-stroke}' fill='#{$sl-btn-fill}' stroke-width='#{$sl-btn-stroke-width}' stroke-linecap='round'/></svg>");
			}//sl-next

		}//button

	}//sl-navigation


	.sl-close{
		background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 66 66'><g id='close' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd' transform='translate(-5.000000, -5.000000)'><polygon fill='#{$sl-btn-close-fill}' points='38 33.7573593 66.2340187 5.5233406 70.4766594 9.76598128 42.2426407 38 70.4766594 66.2340187 66.2340187 70.4766594 38 42.2426407 9.76598128 70.4766594 5.5233406 66.2340187 33.7573593 38 5.5233406 9.76598128 9.76598128 5.5233406'></polygon></g></svg>");

		background-size: 20px 20px;
		background-repeat: no-repeat; 
		background-position: center;
		font-size: 0 !important; 

		&:hover, &:active, &:focus, &:active:focus{
			background-color: transparent;
		}

	}//sl-close

}//sl-wrapper



.simple-lightbox + .sl-overlay{
	background: $sl-overlay-background;
	opacity: 1;
}//sl-overlay


