/*-------------------------------------------------*/
/*	ROTATE
/*-------------------------------------------------*/
.h-rotate{
	@extend .hover-element;


	.card-img{
		transition-duration: .6s;
	}//card-img


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1.4) rotate(-10deg);
		}//card-img

	}//:hover

}//h-rotate






/*-------------------------------------------------*/
/*	ROTATE FADE
/*-------------------------------------------------*/
.h-rotate-fade{
	@extend .hover-element;


	.card-img{
		transition-duration: .6s;
	}//card-img

	.card-img-overlay{
		opacity: 0;
		visibility: hidden;
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1.4) rotate(-10deg);
		}//card-img


		.card-img-overlay{
			opacity: 1;
			visibility: visible;
		}//card-img-overlay

	}//:hover

}//h-rotate-fade








/*-------------------------------------------------*/
/*	ROTATE ZOOM
/*-------------------------------------------------*/
.h-rotate-zoom{
	@extend .h-fade-zoom;


	.card-img{
		transition-duration: .6s;
	}//card-img


	.card-img-overlay > *{
		transition-duration: 1s;
		transform: translate3d(0,0,-400px);
	}//card-img-overlay > *


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img{
			transform: scale(1.4) rotate(-10deg);
		}//card-img

	}//:hover

}//h-rotate-zoom







/*-------------------------------------------------*/
/*	ROTATE 2
/*-------------------------------------------------*/
.h-rotate-2{
	@extend .h-rotate;

	.card-img{
		transition-duration: 4s;
		transition-timing-function: ease;
	}//card-img

}//h-rotate-2






/*-------------------------------------------------*/
/*	ROTATE FADE 2
/*-------------------------------------------------*/
.h-rotate-fade-2{
	@extend .h-rotate-fade;

	.card-img{
		transition-duration: 4s;
		transition-timing-function: ease;
	}//card-img

}//h-rotate-fade-2








/*-------------------------------------------------*/
/*	ROTATE ZOOM
/*-------------------------------------------------*/
.h-rotate-zoom-2{
	@extend .h-rotate-zoom;


	.card-img{
		transition-duration: 4s;
		transition-timing-function: ease;
	}//card-img

}//h-rotate-zoom-2

