/*---------------------------------------------------------*/
/*	NUMBER - Touchspin plugin styles
/*---------------------------------------------------------*/
.input-group.bootstrap-touchspin{


	.input-group-btn{
		flex: 1 0 auto;


		.bootstrap-touchspin-up,
		.bootstrap-touchspin-down{
			background: 		$form-background;
			border: 				solid 1px $form-border-color;
			color: 					$form-color;
			font-size: 			$touchspin-font-size;
			font-weight: 		$touchspin-btn-font-weight;
			width: 					100%;
			height: 				$form-ig-btn-size;
			padding: 				0;
			transition: 		.2s;

			@include linkHover(background $gray-lighter);

		}// bootstrap-touchspin-up / bootstrap-touchspin-down


		.bootstrap-touchspin-down{
			border-right: none;
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}//bootstrap-touchspin-down
	
		.bootstrap-touchspin-up{
			border-left: none;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}//bootstrap-touchspin-up

	}//input-group-btn


	.form-control{
		border-left: none;
		border-right: none;
		padding-left: 5px;
		padding-right: 5px;
		flex: 1 0 auto;
	}





	input[type='number']{
		-moz-appearance: textfield;
	}

	input[type='number']::-webkit-outer-spin-button,
	input[type='number']::-webkit-inner-spin-button{
		-webkit-appearance: none;
	}




	&.input-group-sm{
		.bootstrap-touchspin-up,
		.bootstrap-touchspin-down{
			height: $touchspin-btn-sm-height;
		}
	}//input-group-sm


	&.input-group-lg{
		.bootstrap-touchspin-up,
		.bootstrap-touchspin-down{
			height: $touchspin-btn-lg-height;
		}
	}//input-group-lg

}//bootstrap-touchspin 



.form-control-number[type="number"],
.form-control-number[type="number"]:hover{
	-moz-appearance: textfield;

	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button{
		-webkit-appearance: none;
	}
}