/*---------------------------------------------------------*/
/*  LIST GROUP ITEM
/*---------------------------------------------------------*/
.list-group-item{
	font-size: $list-item-font-size;
	line-height: $list-item-line-height;
	padding: $list-item-padding-y $list-item-padding-x;


	&:first-child{
		border-top-left-radius: $list-item-radius;
		border-top-right-radius: $list-item-radius;
	}//first-child

	&:last-child{
		border-bottom-left-radius: $list-item-radius;
		border-bottom-right-radius: $list-item-radius;
	}//last-child


	// ACTIVE
	&.active{
		background-color: $list-item-active-bg;
		border-color: $list-item-active-border;
		color: $list-item-active-color;

		@include linkHover(
			background-color darken($list-item-active-bg, 10%),
			border-color darken($list-item-active-border, 10%),
			color $list-item-active-color);

	}//active

	// DISABLED
	&.disabled{
		color: $list-item-disabled-color;
	}//disabled

}//list-group-item




.list-group-flush .list-group-item:not(.active){
	background: $list-item-flush-bg;
	border-radius: 0 !important;

	&:first-child{
		border-top-color: transparent;
	}

	&:last-child{
		border-bottom-color: transparent;
	}
}//list-group-flush









/*---------------------------------------------------------*/
/*  LIST GROUP ZEBRA
/*---------------------------------------------------------*/
.list-group.list-group-zebra{

	.list-group-item:nth-child(odd):not(.active){
		background: $list-item-zebra-odd-bg;
	}

	.list-group-item:nth-child(even):not(.active){
		background: $list-item-zebra-even-bg;
	}
}//list-group