/*---------------------------------------------------------*/
/*	FLOATING LABELS
/*	https://github.com/tonystar/bootstrap-float-label
/*---------------------------------------------------------*/
.has-float-label{
	background: #fff;
	border: $form-border solid $form-border-color;
	padding: $form-fl-padding-top $form-fl-padding-x $form-fl-padding-bottom;
  display: block;
	position: relative;


	.form-control:placeholder-shown:not(:focus) + *{
		font-size: .9rem;
		opacity: .6;
		top: 20px;
		left: 14px;
	}


	label, > span{
		position: absolute;
		left: $form-fl-padding-x;
		top: 5px;
		cursor: text;
		font-size: .7rem;
		margin-bottom: 0;
		opacity: 1;
		transition: all .2s;
		pointer-events: none;
	}//label, > span


	.form-control{
		background: transparent;
		border: none;
		padding: 0;
		border-radius: 0;
	}//form-control


	select.form-control{
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
	}//select


	textarea.form-control{
		width: 100%;
		margin-top: 10px;
	}//textarea.form-control



	.form-control::-webkit-input-placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control::-webkit-input-placeholder

	.form-control::-moz-placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control::-moz-placeholder

	.form-control:-ms-input-placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control:-ms-input-placeholder

	.form-control::placeholder{
		color: #ccc;
		opacity: 1;
		transition: all .2s;
	}//form-control::placeholder
	
	.form-control:placeholder-shown:not(:focus)::-webkit-input-placeholder{
		opacity: 0;
	}
	
	.form-control:placeholder-shown:not(:focus)::-moz-placeholder{
		opacity: 0;
	}
	
	.form-control:placeholder-shown:not(:focus):-ms-input-placeholder{
		opacity: 0;
	}
	
	.form-control:placeholder-shown:not(:focus)::placeholder{
		opacity: 0;
	}

}//.has-float-label


