/*---------------------------------------------------------*/
/*  TABLE
/*---------------------------------------------------------*/
.table{
	font-size: $table-font-size;




	/*---------------------------------------------------------*/
	/*  thead
	/*---------------------------------------------------------*/
	thead{

		th, td{
			font-size: $th-font-size;
		}//th

	}//thead
	





	/*---------------------------------------------------------*/
	/*  tbody
	/*---------------------------------------------------------*/
	tbody{

		tr{
			transition: $tr-hover-transition;
		}

		td{
			font-size: $td-font-size;
			line-height: 1.2;
		}//td

	}//tbody





	/*---------------------------------------------------------*/
	/*  tfoot
	/*---------------------------------------------------------*/
	tfoot{
		
		th, td{
			font-weight: 600;
		}//td

	}//tfoot



	caption{
		font-size: $caption-font-size;
	}//caption


}//table.table






/*---------------------------------------------------------*/
/*  TABLE STRIPED COLUMNS
/*---------------------------------------------------------*/
.table-striped-columns{

	tbody > tr > td:nth-of-type(2n+1){
		background: $td-striped;
	}//td


	&.table-dark tbody > tr > td:nth-of-type(2n+2){
		background: $td-striped-dark;
	}// for table-dark


}//table-striped-columns















/*---------------------------------------------------------*/
/*  FLEX TABLES
/*---------------------------------------------------------*/
.rtable{

	tr{ transition: 0s; }

	.th{ display: none; }

}//rtable




@media screen and (max-width: 576px) {

	.rtable{

		thead,
		tfoot{
			display: none;
		}//thead


		.th{
			font-size: $th-font-size;
			font-weight: 600;
			display: block;
		}//th


		tr{
			display: block;
		}//tr


		td, th{
			padding-top: 2px;
			padding-bottom: 2px;
			display: flex;
			justify-content: space-between;


			&:first-child{
				padding-top: 10px;
			}//:first-child


			&:last-child{
				padding-bottom: 10px;
			}//:last-child


			&:not(:first-child){
				border-top: none;
			}//:not(:first-child)

			> *{
				flex: 1 0 0;
				text-overflow: ellipsis;
				overflow: hidden;
			}// > *

		}// td, th


		caption{
			padding-left: 0.75rem;
			padding-right: 0.75rem;
		}//caption

	}//rtable


}//max-width: 768px