/*---------------------------------------------------------*/
/*	SOLID OVERLAY
/*---------------------------------------------------------*/
@mixin overlayColor(
	$bg,
	$opacity
){
	content: '';
	background-color: $bg;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	position: absolute;
	opacity: $opacity;
	border-radius: inherit;
	z-index: 1;
}


.ov-d::after{ @include overlayColor(#000, .5); }
.ov-d-25::after{ @include overlayColor(#000, .25); }
.ov-d-75::after{ @include overlayColor(#000, .75); }

.ov-l::after{ @include overlayColor(#fff, .5); }
.ov-l-25::after{ @include overlayColor(#fff, .25); }
.ov-l-75::after{ @include overlayColor(#fff, .75); }









/*---------------------------------------------------------*/
/*	GRADIENT OVERLAY
/*---------------------------------------------------------*/
@mixin overlayGradient(
	$color-start: #000,
	$color-end: #000,
	$deg: 180deg,
	$width: 100%,
	$height: 100%,
	$left: 0,
	$right: auto,
	$bottom: 0,
	$top: auto
){
	content: '';
	@include gradient-directional(rgba($color-start,0), $color-end, $deg);
	width: $width;
	height: $height;
	left: $left;
	right: $right;
	top: $top;
	bottom: $bottom;
	position: absolute;
	border-radius: inherit;
	z-index: 1;
}



.ov-g-d::after{ @include overlayGradient(); }
.ov-g-d-50::after{ @include overlayGradient(#000, #000, 180deg, 100%, 50%); }

.ov-g-d-top::after{ @include overlayGradient(#000, #000, 360deg, 100%, 100%, 0, auto, auto, 0); }
.ov-g-d-top-50::after{ @include overlayGradient(#000, #000, 360deg, 100%, 50%, 0, auto, auto, 0); }

.ov-g-d-left::after{ @include overlayGradient(#000, #000, 270deg, 100%, 100%); }
.ov-g-d-left-50::after{ @include overlayGradient(#000, #000, 270deg, 50%, 100%); }

.ov-g-d-right::after{ @include overlayGradient(#000, #000, 90deg, 100%, 100%); }
.ov-g-d-right-50::after{ @include overlayGradient(#000, #000, 90deg, 50%, 100%, auto, 0); }



.ov-g-l::after{ @include overlayGradient(#fff, #fff); }
.ov-g-l-50::after{ @include overlayGradient(#fff, #fff, 180deg, 100%, 50%); }

.ov-g-l-top::after{ @include overlayGradient(#fff, #fff, 360deg, 100%, 100%, 0, auto, auto, 0); }
.ov-g-l-top-50::after{ @include overlayGradient(#fff, #fff, 360deg, 100%, 50%, 0, auto, auto, 0); }

.ov-g-l-left::after{ @include overlayGradient(#fff, #fff, 270deg, 100%, 100%); }
.ov-g-l-left-50::after{ @include overlayGradient(#fff, #fff, 270deg, 50%, 100%); }

.ov-g-l-right::after{ @include overlayGradient(#fff, #fff, 90deg, 100%, 100%); }
.ov-g-l-right-50::after{ @include overlayGradient(#fff, #fff, 90deg, 50%, 100%, auto, 0); }











