/*---------------------------------------------------------*/
/*	BUTTONS
/*---------------------------------------------------------*/
$btn-background: 						transparent 						!default;
$btn-color: 								#fff                    !default;
$btn-font-size: 						.8rem 									!default;
$btn-font-weight: 					300 										!default;
$btn-spacer-y: 							6px											!default;
$btn-spacer-x: 							24px 										!default;
$btn-transition: 						.2s ease-in-out 				!default;

$btn-cons-width: 						120px 									!default;

$btn-icon-width: 						38px 										!default;
$btn-icon-height: 					38px 										!default;
$btn-icon-padding: 					0		 										!default;
$btn-icon-font-size: 				18px 										!default;
$btn-icon-line-height: 			38px		 								!default;

$btn-lg-font-size: 					$btn-font-size * 1.5 		!default;
$btn-lg-font-weight: 				$btn-font-weight				!default;
$btn-lg-padding-y: 					10px 										!default;
$btn-lg-padding-x: 					50px 										!default;

$btn-sm-font-size: 					$btn-font-size * .7 		!default;
$btn-sm-font-weight: 				$btn-font-weight				!default;
$btn-sm-padding-y: 					6px 										!default;
$btn-sm-padding-x: 					20px 										!default;

$btn-dropdown-arrow: 				6px 										!default;
$dropdown-min-width: 				180px 									!default;
$dropdown-font-size: 				.9rem										!default;
$dropdown-border-color: 		transparent							!default;
$dropdown-border-radius: 		$global-radius					!default;
$dropdown-item-padding-y: 	5px		 									!default;
$dropdown-item-padding-x: 	20px		 								!default;


$btn-close-font-size: 			36px !default;
