/*---------------------------------------------------------*/
/*  POPOVERS
/*---------------------------------------------------------*/
[data-toggle="popover"]{cursor: pointer;}


.popover{
  background-color: $popover-background;
  font-size: $popover-font-size;
  padding: 0;
  border-radius: $popover-radius;
  box-shadow: $popover-shadow;
}


.popover-title{
  background-color: $popover-title-background;
  border-bottom: $popover-title-border;
  font-size: $popover-title-font-size;
  font-weight: $popover-title-font-weight;
  padding: $popover-title-spacer-y $popover-title-spacer-x 0;
  border-radius: ($popover-radius - 1) ($popover-radius - 1) 0 0;
}//popover-title


.popover-content{
  padding: $popover-content-spacer-y $popover-content-spacer-x;

  p:last-child{
    margin-bottom: 0;
  }//p:last-child

}//popover-content


.popover-title + .popover-content{
  padding-top: $popover-content-spacer-y / 2;
}