/*-------------------------------------------------*/
/*	FADE
/*-------------------------------------------------*/
.h-fade{
	@extend .hover-element;

	.card-img-overlay{
		opacity: 0;
		visibility: hidden;
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img-overlay{
			opacity: 1;
			visibility: visible;
		}//card-img-overlay

	}//:hover

}//h-fade









/*-------------------------------------------------*/
/*	FADE UP
/*-------------------------------------------------*/
.h-fade-up{
	@extend .hover-element;

	.card-img-overlay{
		opacity: 0;
		visibility: hidden;
		transition-duration: .4s;
		transform: translate3d(0,25%,0);
	}//card-img-overlay


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img-overlay{
			opacity: 1;
			visibility: visible;
			transform: translate3d(0,0,0);
		}//card-img-overlay

	}//:hover

}//h-fade-up








