/*-------------------------------------------------*/
/*	FADE ZOOM
/*-------------------------------------------------*/
.h-fade-zoom{
	@extend .hover-element;

	.card-img-overlay{
		opacity: 0;
		visibility: hidden;
		perspective: 900px;
		border-radius: 0;
	}//card-img-overlay

	.card-img-overlay > *{
		transition-duration: .4s;
		transform: translate3d(0,0,300px);
	}//card-img-overlay > *


	/*----------------------- HOVER -----------------------*/
	&:hover{

		.card-img-overlay{
			opacity: 1;
			visibility: visible;
		}//card-img-overlay

		.card-img-overlay > *{
			transform: translate3d(0,0,0);
		}//card-img-overlay

	}//:hover

}//h-fade-zoom







